import React from "react";
import { Text, TextTypes } from "@common";
import styled from "astroturf";

interface InfoProps {
  isExpandable: boolean;
  onClick?: () => void;
  json: object;
}

const Info = React.memo(({ json, isExpandable, onClick }: InfoProps) => {
  const keysLength = React.useMemo(() => Object.keys(json).length, [json]);
  const onCopy = React.useCallback(() => {
    const el = document.createElement("textarea");
    el.value = JSON.stringify(json);
    document.body.appendChild(el);
    el.select();
    document.execCommand("copy");
    document.body.removeChild(el);
  }, [json]);

  const textProps = React.useMemo(() => (isExpandable ? { onClick } : {}), [
    isExpandable,
    onClick
  ]);

  return (
    <Wrapper>
      <Text type={TextTypes.TEXT} {...textProps}>
        {Array.isArray(json)
          ? ` [] ${keysLength} items`
          : ` {} ${keysLength} keys`}
      </Text>
      <CopyButton>{isExpandable ? <Copy onClick={onCopy} /> : null}</CopyButton>
    </Wrapper>
  );
});

const Wrapper = /*#__PURE__*/styled("span", null, {
  displayName: "Wrapper",
  styles: require("./Info-Wrapper.css"),
  attrs: null,
  vars: []
});

// Transparent copy button (heighlight on hover)
const CopyButton = /*#__PURE__*/styled("span", null, {
  displayName: "CopyButton",
  styles: require("./Info-CopyButton.css"),
  attrs: null,
  vars: []
});

const Copy = /*#__PURE__*/styled("span", null, {
  displayName: "Copy",
  styles: require("./Info-Copy.css"),
  attrs: null,
  vars: []
});

export { Info };
