import React from "react";
import styled from "astroturf";

import { isDarkTheme } from "@settings";
import { colors } from "@colors";

export type ButtonMods = "default" | "mini" | "circle" | "disabled";

const baseStyle: React.CSSProperties = {
  color: isDarkTheme ? colors.buttonText.dark : colors.buttonText.light,
  background: isDarkTheme
    ? colors.buttonBackground.dark
    : colors.buttonBackground.light,
  border: `1px solid ${isDarkTheme ? colors.border.dark : colors.border.light}`
};

const mods: { [key in ButtonMods]: React.CSSProperties } = {
  default: baseStyle,
  mini: {
    ...baseStyle,
    padding: "0rem 0.2rem",
    fontSize: "0.8rem"
  },
  circle: {
    ...baseStyle,
    borderRadius: "50%"
  },
  disabled: {
    color: isDarkTheme ? colors.disabled.dark : colors.disabled.light
  }
};

interface ButtonProps {
  title?: string;
  type?: ButtonMods;
  onClick: () => void;
  children?: React.ElementType;
  disabled?: boolean;
}

const Button = React.memo(
  ({
    title = null,
    onClick,
    type = "default",
    children = null,
    disabled = false,
    ...rest
  }: ButtonProps) => (
    <ButtonStyled
      style={{ ...mods[type], ...(disabled ? mods.disabled : {}) }}
      onClick={disabled ? () => {} : onClick}
      {...rest}
    >
      {title ? title : children}
    </ButtonStyled>
  )
);

const ButtonStyled = /*#__PURE__*/styled("button", null, {
  displayName: "ButtonStyled",
  styles: require("./Button-ButtonStyled.css"),
  attrs: null,
  vars: []
});

export { Button };
